import React, { createContext, useContext, ReactNode } from 'react';
import type { CartItem, Product } from '../types';
import { usePage, router } from '@inertiajs/react';

interface CartContextType {
  items: CartItem[];
  stallId: number | string | null;
  addItem: (product: Product, quantity: number) => { success: boolean; error?: string };
  removeItem: (productId: number) => void; // Rails IDs are numbers
  updateQuantity: (productId: number, quantity: number) => void;
  clearCart: () => void;
  total: number;
  itemCount: number;
}

const CartContext = createContext<CartContextType | undefined>(undefined);

export function CartProvider({ children }: { children: ReactNode }) {
  const { cart, auth } = usePage().props as any;
  const items: CartItem[] = cart?.items || [];
  const itemCount = cart?.item_count || 0;
  const total = cart?.total || 0;
  const stallId = cart?.stallId || null;

  const addItem = (product: Product, quantity: number) => {
    if (!auth?.user) {
      return { success: false, error: 'Trebuie s te autentifici pentru a aduga produse n co. Te rugm s te conectezi sau s i creezi un cont.' };
    }

    // Basic frontend check for UX, but backend handles it too
    if (stallId && String(stallId) !== String(product.stallId)) {
       return { success: false, error: 'Produsul este de la o alt tarab. Golete coul actual pentru a aduga.' };
    }

    router.post('/cart/add_item', { product_id: product.id, quantity }, { preserveScroll: true });
    return { success: true };
  };

  const removeItem = (productId: number) => {
    router.delete(`/cart/remove_item/${productId}`, { preserveScroll: true });
  };

  const updateQuantity = (productId: number, quantity: number) => {
    router.patch(`/cart/update_item/${productId}`, { quantity }, { preserveScroll: true });
  };

  const clearCart = () => {
    router.delete('/cart/clear');
  };

  return (
    <CartContext.Provider value={{ items, stallId, addItem, removeItem, updateQuantity, clearCart, total, itemCount }}>
      {children}
    </CartContext.Provider>
  );
}

export function useCart() {
  const context = useContext(CartContext);
  if (context === undefined) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
}
