import { useState } from 'react';
import { Link } from '@inertiajs/react';
import { 
  Play, 
  Store, 
  ShoppingBag, 
  Truck, 
  DollarSign,
  ChevronDown,
  ChevronUp
} from 'lucide-react';
import { Button } from '../components/ui/button';
import { cn } from '../lib/utils';
import { SellerSimulator } from '../components/demo/SellerSimulator';
import { BuyerSimulator } from '../components/demo/BuyerSimulator';

export default function DemoPage() {
  // FAQ State
  const [openFaq, setOpenFaq] = useState<number | null>(null);

  const faqs = [
    {
      question: "Ct cost s mi deschid o tarab?",
      answer: "Absolut nimic! Platforma este complet gratuit pentru vnztori. Nu percepem comisioane din vnzri i nu exist taxe de abonament. Scopul nostru este s susinem productorii locali."
    },
    {
      question: "Cum primesc banii pe comenzi?",
      answer: "Plata se face momentan exclusiv 'Ramburs' (cash la livrare/ridicare). Tu, ca vnztor, ncasezi banii direct de la client atunci cnd i predai produsele. Simplu i direct."
    },
    {
      question: "Trebuie s am firm (SRL/PFA)?",
      answer: "Nu este obligatoriu din partea platformei, dar eti responsabil s respeci legislaia n vigoare privind comerul cu produse alimentare. Platforma conecteaz direct productorii cu consumatorii."
    },
    {
      question: "Pot vinde oriunde n ar?",
      answer: "Sistemul este gndit momentan pentru comuniti locale. Setezi oraul n care activezi, iar clienii te vor gsi dac filtreaz dup acel ora. Recomandm livrarea personal sau ridicarea din locaie."
    },
    {
      question: "Ce se ntmpl dac nu pot onora o comand?",
      answer: "Poi anula comanda din panoul de administrare, specificnd un motiv. Clientul va fi notificat automat. Este important s menii o comunicare bun pentru a avea recenzii pozitive."
    },
     {
      question: "Pot avea i cont de cumprtor?",
      answer: "Da! Acelai cont poate fi folosit att pentru a vinde produse (prin Taraba ta), ct i pentru a cumpra bunti de la ali productori locali."
    }
  ];

  return (
    <div className="min-h-screen bg-cream text-stone-800 font-sans">
      
      {/* Hero Section */}
      <section className="bg-stone-900 text-white py-20 px-4 text-center relative overflow-hidden">
        <div className="absolute inset-0 opacity-10 pointer-events-none">
             <div className="absolute top-10 left-10 w-32 h-32 bg-fern rounded-full blur-3xl"></div>
             <div className="absolute bottom-10 right-10 w-48 h-48 bg-marigold rounded-full blur-3xl"></div>
        </div>
        
        <div className="max-w-4xl mx-auto relative z-10">
          <h1 className="text-4xl md:text-6xl font-serif font-bold mb-6">
            Deschide-i Taraba Digital n 3 Minute
          </h1>
          <p className="text-lg md:text-xl text-stone-300 mb-8 max-w-2xl mx-auto">
            Vinde produse locale, de cas, direct ctre vecinii ti. Fr comisioane, fr bti de cap.
            Simplu, ca la pia.
          </p>
          <div className="flex justify-center gap-4">
            <button onClick={() => document.getElementById('simulator')?.scrollIntoView({ behavior: 'smooth' })} className="bg-fern hover:bg-green-700 text-white px-8 py-3 rounded-full font-bold transition-colors shadow-lg flex items-center">
               <Play className="w-5 h-5 mr-2 fill-current" /> ncearc Simulatorul
            </button>
            <Link href="/" className="bg-white/10 hover:bg-white/20 text-white border border-white/30 px-8 py-3 rounded-full font-bold transition-colors">
               Exploreaz Piaa
            </Link>
          </div>
        </div>
      </section>

      {/* Interactive Simulator */}
      <section id="simulator" className="py-20 px-4 bg-white relative">
        <div className="max-w-7xl mx-auto">
           <div className="text-center mb-16">
              <h2 className="text-3xl font-serif font-bold mb-4 text-stone-800">Test Drive: Cum Funcioneaz?</h2>
              <p className="text-stone-500">ncearc interfaa noastr interactiv. Nu ai nevoie de cont real!</p>
           </div>

           <div className="grid lg:grid-cols-2 gap-12 lg:gap-20 items-start">
               {/* Buyer Side (Mobile) */}
               <div>
                   <BuyerSimulator />
               </div>

               {/* Seller Side (Desktop) */}
               <div className="hidden lg:block relative top-0 lg:top-10">
                   <SellerSimulator />
               </div>
               
               {/* Mobile fallback for seller view if needed, but keeping it hidden on mobile to prioritize buyer view as it's cleaner, or stack them? 
                   User asked for left/right. On mobile they will stack. 
               */}
               <div className="lg:hidden">
                   <SellerSimulator />
               </div>
           </div>
           
        </div>
      </section>

      {/* Why Us */}
      <section className="py-20 bg-stone-50">
          <div className="container mx-auto px-4 max-w-5xl">
              <h2 className="text-3xl font-serif font-bold text-center mb-16 text-stone-800">De ce s alegi Piaa Online?</h2>
              
              <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                  <div className="bg-white p-6 rounded-xl shadow-sm border border-stone-100 hover:-translate-y-1 transition-transform">
                      <div className="w-12 h-12 bg-green-100 rounded-lg flex items-center justify-center mb-4 text-fern">
                          <DollarSign className="w-6 h-6" />
                      </div>
                      <h3 className="font-bold text-xl mb-2">0% Comision</h3>
                      <p className="text-stone-600">Tot ce ctigi este al tu. Nu percepem taxe din vnzri. Platforma este susinut de comunitate.</p>
                  </div>

                   <div className="bg-white p-6 rounded-xl shadow-sm border border-stone-100 hover:-translate-y-1 transition-transform">
                      <div className="w-12 h-12 bg-blue-100 rounded-lg flex items-center justify-center mb-4 text-blue-600">
                          <Store className="w-6 h-6" />
                      </div>
                      <h3 className="font-bold text-xl mb-2">Prezen Digital</h3>
                      <p className="text-stone-600">i oferim propriul tu magazin online, link personalizat i instrumente profesionale, gratuit.</p>
                  </div>

                   <div className="bg-white p-6 rounded-xl shadow-sm border border-stone-100 hover:-translate-y-1 transition-transform">
                      <div className="w-12 h-12 bg-orange-100 rounded-lg flex items-center justify-center mb-4 text-orange-600">
                          <Truck className="w-6 h-6" />
                      </div>
                      <h3 className="font-bold text-xl mb-2">Livrare Flexibil</h3>
                      <p className="text-stone-600">Tu stabileti regulile. Predare personal, livrare la domiciliu, sau punct de ntlnire.</p>
                  </div>
              </div>
          </div>
      </section>

      {/* FAQ */}
      <section className="py-20 bg-white">
          <div className="container mx-auto px-4 max-w-3xl">
              <h2 className="text-3xl font-serif font-bold text-center mb-12 text-stone-800">ntrebri Frecvente</h2>
              
              <div className="space-y-4">
                  {faqs.map((faq, index) => (
                      <div key={index} className="border border-stone-200 rounded-xl overflow-hidden">
                          <button 
                            onClick={() => setOpenFaq(openFaq === index ? null : index)}
                            className="w-full text-left p-4 md:p-6 flex justify-between items-center bg-stone-50 hover:bg-stone-100 transition-colors"
                          >
                              <span className="font-bold text-stone-800">{faq.question}</span>
                              {openFaq === index ? <ChevronUp className="w-5 h-5 text-stone-400" /> : <ChevronDown className="w-5 h-5 text-stone-400" />}
                          </button>
                          
                          <div 
                            className={cn(
                                "transition-all duration-300 ease-in-out overflow-hidden bg-white",
                                openFaq === index ? "max-h-48 opacity-100" : "max-h-0 opacity-0"
                            )}
                          >
                              <div className="p-4 md:p-6 text-stone-600 leading-relaxed border-t border-stone-100">
                                  {faq.answer}
                              </div>
                          </div>
                      </div>
                  ))}
              </div>
          </div>
      </section>

      {/* Bottom CTA */}
      <section className="bg-fern text-white py-16 text-center">
          <div className="container mx-auto px-4">
              <h2 className="text-3xl font-serif font-bold mb-6">Gata s ncepi?</h2>
              <p className="text-green-100 mb-8 max-w-xl mx-auto">Nu te cost nimic s ncerci. Altur-te comunitii de productori locali astzi.</p>
              <div className="flex flex-col sm:flex-row justify-center gap-4">
                  <a href="/auth/google_oauth2" className="bg-white text-fern hover:bg-stone-100 px-8 py-3 rounded-full font-bold shadow-lg transition-transform hover:scale-105">
                      Deschide o Tarab Acum
                  </a>
              </div>
          </div>
      </section>
    </div>
  );
}
