import { Link, router } from '@inertiajs/react';
import { AdminLayout } from '../../layouts/AdminLayout';
import { Button } from '../../components/ui/button';
import { Check, X, Trash2, Store, MapPin, Calendar, ExternalLink, Eye } from 'lucide-react';

interface Stall {
  id: number;
  name: string;
  ownerName: string;
  ownerEmail: string;
  status: 'pending' | 'approved' | 'rejected';
  created_at: string;
  location: string;
}

interface AdminStallsPageProps {
  stalls: Stall[];
}

export default function AdminStallsPage({ stalls }: AdminStallsPageProps) {
  
  const handleApprove = (id: number) => {
    if (confirm('Aprobai aceast tarab? va deveni vizibil public.')) {
        router.patch(`/admin/stalls/${id}/approve`);
    }
  };

  const handleReject = (id: number) => {
    if (confirm('Respingei aceast tarab?')) {
        router.patch(`/admin/stalls/${id}/reject`);
    }
  };
  
  const handleDelete = (id: number) => {
      if (confirm('Sigur dorii s tergei aceast tarab? Aceast aciune este ireversibil.')) {
          router.delete(`/admin/stalls/${id}`);
      }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
           <h1 className="text-2xl font-serif font-bold text-stone-800">Administrare Tarabe</h1>
           <p className="text-stone-500">Aprobare i gestionare comerciani.</p>
        </div>
        <div className="space-x-2">
            <Button variant="outline" onClick={() => router.get('/admin/stalls?status=pending')}>De Aprobat</Button>
            <Button variant="outline" onClick={() => router.get('/admin/stalls')}>Toate</Button>
        </div>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-stone-100 overflow-hidden">
        <table className="min-w-full divide-y divide-stone-100">
          <thead className="bg-stone-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase tracking-wider">Tarab</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase tracking-wider">Proprietar</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase tracking-wider">Status</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase tracking-wider">Data nscrierii</th>
              <th className="px-6 py-3 text-right text-xs font-bold text-stone-500 uppercase tracking-wider">Aciuni</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-stone-100">
            {stalls.map((stall) => (
              <tr key={stall.id} className="hover:bg-stone-50 transition-colors">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex items-center">
                    <div className="h-10 w-10 flex-shrink-0 bg-stone-100 rounded-lg flex items-center justify-center text-stone-400">
                      <Store className="h-6 w-6" />
                    </div>
                    <div className="ml-4">
                      <div className="text-sm font-medium text-stone-900">{stall.name}</div>
                      <div className="text-xs text-stone-500 flex items-center">
                        <MapPin className="h-3 w-3 mr-1" /> {stall.location}
                      </div>
                    </div>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-stone-900">{stall.ownerName}</div>
                  <div className="text-xs text-stone-500">{stall.ownerEmail}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={`px-2 py-1 inline-flex text-xs leading-5 font-semibold rounded-full capitalize
                    ${stall.status === 'approved' ? 'bg-green-100 text-green-800' : 
                      stall.status === 'pending' ? 'bg-yellow-100 text-yellow-800' : 'bg-red-100 text-red-800'}
                  `}>
                    {stall.status === 'approved' ? 'Aprobat' : stall.status === 'pending' ? 'n Ateptare' : 'Respins'}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-stone-500">
                   {new Date(stall.created_at).toLocaleDateString('ro-RO')}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <div className="flex justify-end space-x-2">
                    {stall.status === 'pending' && (
                        <>
                            <button onClick={() => handleApprove(stall.id)} className="text-green-600 hover:text-green-900 bg-green-50 p-1.5 rounded-md" title="Aprob">
                                <Check className="h-4 w-4" />
                            </button>
                            <button onClick={() => handleReject(stall.id)} className="text-red-600 hover:text-red-900 bg-red-50 p-1.5 rounded-md" title="Respinge">
                                <X className="h-4 w-4" />
                            </button>
                        </>
                    )}
                    <Link href={`/admin/stalls/${stall.id}`} className="text-stone-400 hover:text-fern p-1.5" title="Vezi Detalii Admin">
                        <Eye className="h-4 w-4" />
                    </Link>
                    <Link href={`/stalls/${stall.id}`} className="text-stone-400 hover:text-fern p-1.5" title="Vezi Public">
                        <ExternalLink className="h-4 w-4" />
                    </Link>
                    <button onClick={() => handleDelete(stall.id)} className="text-stone-400 hover:text-red-600 p-1.5" title="terge">
                        <Trash2 className="h-4 w-4" />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {stalls.length === 0 && (
            <div className="p-12 text-center text-stone-500">Nu au fost gsite tarabe.</div>
        )}
      </div>
    </div>
  );
}

AdminStallsPage.layout = (page: any) => <AdminLayout>{page}</AdminLayout>;
