import { Link } from '@inertiajs/react';
import { AdminLayout } from '../../layouts/AdminLayout';
import { User, Store, ShoppingBag, MapPin, Mail, Calendar, ArrowLeft } from 'lucide-react';

interface OrderSummary {
  id: number;
  created_at: string;
  total_cents: number;
  status: string;
  stallName: string;
  total: number;
}

interface UserDetailProps {
  user: {
    id: number;
    name: string;
    email: string;
    avatar_url: string;
    location: string;
    created_at: string;
  };
  orders: OrderSummary[];
}

import { router } from '@inertiajs/react'; // Add missing import

export default function UserDetailPage({ user, orders }: UserDetailProps) {
  const handleDelete = () => {
    if (confirm('Sigur dorii s tergei acest utilizator? Aceast aciune este ireversibil.')) {
        router.delete(`/admin/users/${user.id}`);
    }
  };

  return (
    <div className="space-y-8">
      <Link href="/admin/users" className="flex items-center text-stone-500 hover:text-stone-900 transition-colors mb-4">
         <ArrowLeft className="h-4 w-4 mr-2" /> napoi la Utilizatori
      </Link>

      {/* User Profile Card */}
      <div className="bg-white rounded-xl shadow-sm border border-stone-100 p-8 flex flex-col md:flex-row items-center md:items-start gap-8">
         <img src={user.avatar_url} alt={user.name} className="w-32 h-32 rounded-full border-4 border-stone-50" />
         <div className="flex-1 text-center md:text-left">
            <h1 className="text-3xl font-serif font-bold text-stone-800 mb-2">{user.name}</h1>
            <div className="flex flex-col md:flex-row gap-4 text-stone-500 items-center md:items-start">
               <span className="flex items-center"><Mail className="h-4 w-4 mr-2" /> {user.email}</span>
               <span className="flex items-center"><MapPin className="h-4 w-4 mr-2" /> {user.location || 'Fr locaie'}</span>
               <span className="flex items-center"><Calendar className="h-4 w-4 mr-2" /> nregistrat pe {new Date(user.created_at).toLocaleDateString('ro-RO')}</span>
            </div>
         </div>
         <button 
            onClick={handleDelete}
            className="flex items-center px-4 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 transition-colors font-medium border border-red-100"
         >
            <User className="h-4 w-4 mr-2" /> terge Utilizator
         </button>
      </div>

      {/* Orders History */}
      <div>
         <h2 className="text-xl font-bold font-serif text-stone-800 mb-4 flex items-center">
            <ShoppingBag className="h-5 w-5 mr-2 text-fern" /> Istoric Comenzi ({orders.length})
         </h2>
         <div className="bg-white rounded-xl shadow-sm border border-stone-100 overflow-hidden">
            <table className="min-w-full divide-y divide-stone-100">
              <thead className="bg-stone-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">ID</th>
                  <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Dat</th>
                  <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Tarab</th>
                  <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Total</th>
                  <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Status</th>
                  <th className="px-6 py-3 text-right text-xs font-bold text-stone-500 uppercase">Aciuni</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-stone-100">
                {orders.map((order) => (
                   <tr key={order.id} className="hover:bg-stone-50">
                      <td className="px-6 py-4 text-sm font-medium text-stone-900">#{order.id}</td>
                      <td className="px-6 py-4 text-sm text-stone-500">{new Date(order.created_at).toLocaleDateString('ro-RO')}</td>
                      <td className="px-6 py-4 text-sm text-stone-900 font-medium">{order.stallName}</td>
                      <td className="px-6 py-4 text-sm font-bold text-stone-900">{order.total} RON</td>
                      <td className="px-6 py-4">
                         <span className={`text-xs px-2 py-1 rounded-full font-bold uppercase
                            ${order.status === 'completed' ? 'bg-green-100 text-green-700' :
                              order.status === 'new_order' ? 'bg-blue-100 text-blue-700' :
                              order.status === 'cancelled' ? 'bg-red-100 text-red-700' : 'bg-yellow-100 text-yellow-700'}
                         `}>
                            {order.status}
                         </span>
                      </td>
                      <td className="px-6 py-4 text-right">
                         <Link href={`/admin/orders/${order.id}`} className="text-fern hover:underline text-sm font-medium">
                            Vezi Tranzacia
                         </Link>
                      </td>
                   </tr>
                ))}
                {orders.length === 0 && (
                    <tr>
                        <td colSpan={6} className="px-6 py-12 text-center text-stone-500">Nu exist comenzi.</td>
                    </tr>
                )}
              </tbody>
            </table>
         </div>
      </div>
    </div>
  );
}

UserDetailPage.layout = (page: any) => <AdminLayout>{page}</AdminLayout>;
