import React from 'react';
import { Link } from '@inertiajs/react';
import { useAuth } from '../context/AuthContext';
import { cn } from '../lib/utils';
import { Package, ChevronRight, Calendar } from 'lucide-react';
import { Button } from '../components/ui/button';

export default function OrdersPage({ orders = [] }: { orders: any[] }) {
  const { user } = useAuth();
  
  if (!user) {
    return <div className="p-12 text-center text-stone-500">Trebuie s te autentifici pentru a vedea comenzile.</div>;
  }

  const getStatusColor = (status: string) => {
     switch(status) {
         case 'new_order': return 'bg-yellow-100 text-yellow-800';
         case 'confirmed': return 'bg-blue-100 text-blue-800';
         case 'completed': return 'bg-green-100 text-green-800';
         case 'cancelled': return 'bg-red-100 text-red-800';
         default: return 'bg-stone-100 text-stone-800';
     }
  };

  const getStatusLabel = (status: string) => {
      switch(status) {
         case 'new_order': return 'Comand Nou';
         case 'confirmed': return 'Confirmat';
         case 'completed': return 'Finalizat';
         case 'cancelled': return 'Anulat';
         default: return status;
     }
  };

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-serif font-bold text-stone-800 mb-8">Comenzile Mele</h1>
      
      {orders.length === 0 ? (
         <div className="bg-white rounded-lg p-12 text-center border border-stone-100 shadow-sm">
             <Package className="h-12 w-12 mx-auto text-stone-300 mb-4" />
             <h3 className="text-lg font-medium text-stone-700">Nu ai plasat nicio comand.</h3>
             <Link href="/" className="mt-4 inline-block">
                <Button>ncepe Cumprturile</Button>
             </Link>
         </div>
      ) : (
         <div className="space-y-4">
            {orders.map(order => (
               <Link key={order.id} href={`/orders/${order.id}`} className="block bg-white rounded-lg border border-stone-100 shadow-sm hover:shadow-md transition-shadow p-6 group">
                   <div className="flex flex-col md:flex-row justify-between md:items-center gap-4">
                      <div>
                          <div className="flex items-center gap-3 mb-2">
                             <span className="font-bold text-lg text-fern">#{order.id}</span>
                             <span className={cn("px-2.5 py-0.5 rounded-full text-xs font-medium", getStatusColor(order.status))}>
                                {getStatusLabel(order.status)}
                             </span>
                          </div>
                          <div className="flex items-center text-sm text-stone-500">
                             <Calendar className="h-4 w-4 mr-1" />
                             {new Date(order.created_at).toLocaleDateString('ro-RO')}
                             <span className="mx-2"></span>
                             {order.stall?.name || 'Tarab'}
                          </div>
                      </div>
                      
                      <div className="flex items-center justify-between md:justify-end gap-6 flex-grow">
                          <div className="font-bold text-xl text-stone-800">
                             {(order.total_cents / 100).toFixed(2)} RON
                          </div>
                          <ChevronRight className="h-5 w-5 text-stone-300 group-hover:text-fern transition-colors" />
                      </div>
                   </div>
               </Link>
            ))}
         </div>
      )}
    </div>
  );
}
