import React, { useState } from 'react';
import { Button } from './ui/button';
import { router } from '@inertiajs/react';

interface ProductFormProps {
  product?: any;
  onClose: () => void;
  stallId: number | string;
}

export function ProductForm({ product, onClose, stallId }: ProductFormProps) {
  const [values, setValues] = useState({
    name: product?.name || '',
    description: product?.description || '',
    price: product?.price || '',
    unit: product?.unit || 'kg',
    category: product?.category || 'Legume',
    stock_type: product?.stockType || 'in_stock',
    stock_quantity: product?.stockQuantity || '',
  });

  const [image, setImage] = useState<File | null>(null);

  const handleChange = (e: any) => {
    setValues(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();
    
    // Use FormData for image upload
    const formData = new FormData();
    Object.keys(values).forEach(key => {
      formData.append(`product[${key}]`, (values as any)[key]);
    });
    if (image) formData.append('product[image]', image);

    if (product) {
       // Inertia patch doesn't handle multipart easily in some versions, 
       // but typically we can use post with _method: 'PATCH'
       formData.append('_method', 'PATCH');
       router.post(`/seller/products/${product.id}`, formData, {
         onSuccess: () => onClose()
       });
    } else {
       router.post('/seller/products', formData, {
         onSuccess: () => onClose()
       });
    }
  };

  const categories = ['Ou', 'Lactate', 'Carne', 'Legume', 'Fructe', 'Conserve', 'Afumturi', 'Meteuguri', 'Altele'];
  const units = ['kg', 'bucat', '100g', 'borcan', 'litru'];
  const stockTypes = [
    { value: 'in_stock', label: 'n stoc disponibil' },
    { value: 'limited', label: 'Stoc limitat' },
    { value: 'one_piece', label: 'O bucat' },
    { value: 'out_of_stock', label: 'Stoc epuizat' }
  ];

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block text-sm font-medium text-stone-700">Nume Produs</label>
          <input type="text" name="name" value={values.name} onChange={handleChange} required className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" />
        </div>
        <div>
          <label className="block text-sm font-medium text-stone-700">Categorie</label>
          <select name="category" value={values.category} onChange={handleChange} className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern">
            {categories.map(c => <option key={c} value={c}>{c}</option>)}
          </select>
        </div>
      </div>

      <div>
        <label className="block text-sm font-medium text-stone-700">Descriere</label>
        <textarea name="description" value={values.description} onChange={handleChange} required rows={3} className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" />
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div>
          <label className="block text-sm font-medium text-stone-700">Pre (RON)</label>
          <input type="number" step="0.01" name="price" value={values.price} onChange={handleChange} required className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" />
        </div>
        <div>
          <label className="block text-sm font-medium text-stone-700">Unitate</label>
          <select name="unit" value={values.unit} onChange={handleChange} className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern">
            {units.map(u => <option key={u} value={u}>{u}</option>)}
          </select>
        </div>
        <div>
           <label className="block text-sm font-medium text-stone-700">Stoc</label>
           <select name="stock_type" value={values.stock_type} onChange={handleChange} className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern">
             {stockTypes.map(st => <option key={st.value} value={st.value}>{st.label}</option>)}
           </select>
        </div>
      </div>

      {values.stock_type === 'limited' && (
        <div>
           <label className="block text-sm font-medium text-stone-700">Cantitate n Stoc</label>
           <input type="number" name="stock_quantity" value={values.stock_quantity} onChange={handleChange} required className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" />
        </div>
      )}

      <div>
        <label className="block text-sm font-medium text-stone-700">Imagine Produs</label>
        <input type="file" onChange={(e) => setImage(e.target.files?.[0] || null)} className="mt-1 block w-full text-sm text-stone-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-fern/10 file:text-fern hover:file:bg-fern/20" />
      </div>

      <div className="flex justify-end gap-3 pt-4">
        <Button type="button" variant="outline" onClick={onClose}>Anuleaz</Button>
        <Button type="submit" className="bg-fern text-white">{product ? 'Salveaz Modificrile' : 'Adaug Produs'}</Button>
      </div>
    </form>
  );
}
