import React from 'react';
import { Link, router, usePage } from '@inertiajs/react';
import { useCart } from '../context/CartContext';
import { Button } from '../components/ui/button';
import { Trash2, Plus, Minus, ArrowLeft, MapPin } from 'lucide-react';

export default function CartPage() {
  const { items, stallId, updateQuantity, removeItem, clearCart, total } = useCart();
  const { cart, auth } = usePage().props as any;
  const stallName = cart?.stallName;

  const [tempLocation, setTempLocation] = React.useState(cart?.userLocation || '');
  const [isChangingLocation, setIsChangingLocation] = React.useState(false);

  const CITIES = [
    'Cluj-Napoca', 'Bucureti', 'Timioara', 'Iai', 'Constana', 
    'Craiova', 'Braov', 'Galai', 'Oradea', 'Ploieti', 'Sibiu'
  ];

  const handleCheckout = () => {
    if (total < 5) {
      alert("Valoarea minim a comenzii este 5 RON.");
      return;
    }
    if (confirm("Confirmi plasarea comenzii?")) {
      router.post('/orders', { location: tempLocation });
    }
  };

  if (items.length === 0) {
    return (
      <div className="max-w-4xl mx-auto px-4 py-16 text-center">
        <h2 className="text-2xl font-serif font-bold text-stone-700 mb-4">Coul tu este gol</h2>
        <p className="text-stone-500 mb-8">Nu ai adugat nc produse n co.</p>
        <Link href="/">
          <Button>Exploreaz Tarabele</Button>
        </Link>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto px-4 py-8">
      <Link href={stallId ? `/stalls/${String(stallId)}` : '/'} className="inline-flex items-center text-stone-500 hover:text-fern mb-6">
        <ArrowLeft className="h-4 w-4 mr-1" /> napoi la cumprturi
      </Link>
      
      <div className="bg-white rounded-lg shadow-sm border border-stone-100 overflow-hidden">
        <div className="p-6 border-b border-stone-100 bg-stone-50">
          <h1 className="text-2xl font-serif font-bold text-stone-800">Coul de cumprturi</h1>
          {stallId && (
            <div className="flex items-center mt-2 text-sm text-stone-600">
              Comanzi de la: <Link href={`/stalls/${stallId}`} className="font-bold text-fern hover:underline ml-1">{stallName || 'Tarab'}</Link>
            </div>
          )}
        </div>
        
        <div className="divide-y divide-stone-100">
          {items.map(({ product, quantity }) => (
            <div key={product.id} className="p-6 flex flex-col sm:flex-row items-center gap-4">
              <div className="h-20 w-20 flex-shrink-0 bg-stone-100 rounded-md overflow-hidden">
                {product.image && <img src={product.image} alt={product.name} className="h-full w-full object-cover" />}
              </div>
              
              <div className="flex-grow text-center sm:text-left">
                <h3 className="font-bold text-stone-800">{product.name}</h3>
                <p className="text-sm text-stone-500">{product.price.toFixed(2)} RON / {product.unit || 'buc'}</p>
              </div>
              
              <div className="flex items-center bg-stone-100 rounded-lg p-1">
                <button onClick={() => updateQuantity(Number(product.id), quantity - 1)} className="p-1 hover:bg-white rounded-md transition-colors">
                  <Minus className="h-4 w-4 text-stone-600" />
                </button>
                <span className="w-8 text-center font-medium text-stone-700">{quantity}</span>
                <button onClick={() => updateQuantity(Number(product.id), quantity + 1)} className="p-1 hover:bg-white rounded-md transition-colors">
                  <Plus className="h-4 w-4 text-stone-600" />
                </button>
              </div>
              
              <div className="font-bold text-lg text-stone-800 w-24 text-right">
                {(product.price * quantity).toFixed(2)} RON
              </div>
              
              <button onClick={() => removeItem(Number(product.id))} className="text-stone-400 hover:text-red-500 p-2">
                <Trash2 className="h-5 w-5" />
              </button>
            </div>
          ))}
        </div>
        
        <div className="p-6 bg-stone-50 border-t border-stone-100">
          <div className="mb-6 p-4 bg-white rounded-lg border border-stone-100">
             <div className="flex justify-between items-center mb-2">
                <span className="text-sm font-bold text-stone-600">Adres de livrare</span>
                {!isChangingLocation && (
                   <button onClick={() => setIsChangingLocation(true)} className="text-xs text-fern hover:underline">Schimb Locaia</button>
                )}
             </div>
             {isChangingLocation ? (
                <div className="flex gap-2">
                   <select 
                      className="flex-grow text-sm border-stone-200 rounded-md p-2"
                      value={tempLocation}
                      onChange={(e) => setTempLocation(e.target.value)}
                   >
                      <option value="">Alege oraul...</option>
                      {CITIES.map(c => <option key={c} value={c}>{c}</option>)}
                   </select>
                   <Button size="sm" onClick={() => setIsChangingLocation(false)}>OK</Button>
                </div>
             ) : (
                <div className="flex items-center text-stone-800">
                   <MapPin className="h-4 w-4 mr-2 text-stone-400" />
                   {tempLocation || "Nicio locaie setat"}
                </div>
             )}
          </div>

          <div className="flex justify-between items-center mb-6">
            <span className="text-lg text-stone-600">Total</span>
            <div className="text-right">
               <span className="text-3xl font-serif font-bold text-fern">{total.toFixed(2)} RON</span>
               {total < 5 && <p className="text-[10px] text-red-500 mt-1">Minim 5 RON pentru comand</p>}
            </div>
          </div>
          
            <div className="flex flex-col sm:flex-row gap-4 justify-end">
              <Button variant="ghost" className="text-stone-500" onClick={clearCart}>Golete Coul</Button>
              {auth?.user ? (
                <Button size="lg" onClick={handleCheckout} disabled={total < 5} className="w-full sm:w-auto">
                  Finalizeaz Comanda
                </Button>
              ) : (
                <Link href="/login" className="w-full sm:w-auto">
                  <Button size="lg" className="w-full">
                    Autentific-te pentru a Comanda
                  </Button>
                </Link>
              )}
            </div>
        </div>
      </div>
    </div>
  );
}
