import { useState, useRef, useEffect } from 'react';
import { Link } from '@inertiajs/react';
import { LogOut, User as UserIcon, Store, Package, ShoppingBag, LayoutDashboard, ChevronDown } from 'lucide-react';
import { useAuth } from '../context/AuthContext';

export default function UserMenu() {
  const { user, logout } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  if (!user) return null;

  return (
    <div className="relative" ref={dropdownRef}>
      <button 
        onClick={() => setIsOpen(!isOpen)}
        className={`flex items-center space-x-2 focus:outline-none hover:bg-stone-100 p-1.5 rounded-full transition-all duration-200 border border-transparent hover:border-stone-200 ${isOpen ? 'bg-white border-stone-200 shadow-sm ring-1 ring-stone-950/5' : ''}`}
      >
        <img src={user.avatar} alt={user.name} className="h-8 w-8 rounded-full border border-stone-300" />
        <span className="text-sm font-medium text-stone-700 hidden lg:block">{user.name}</span>
        <ChevronDown className={`h-4 w-4 text-stone-500 transition-transform duration-200 ${isOpen ? 'rotate-180' : ''}`} />
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-56 bg-white rounded-xl shadow-xl border border-gray-100 z-50 py-1">
          <div className="px-4 py-3 border-b border-gray-100">
            <p className="text-sm font-medium text-gray-900">{user.name}</p>
            <p className="text-xs text-gray-500 truncate">{user.email}</p>
          </div>
          
          {user.isAdmin && (
            <div className="py-1">
              <Link 
                href="/admin/dashboard" 
                className="group flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 hover:text-fern"
                onClick={() => setIsOpen(false)}
              >
                <div className="mr-3 p-1 bg-red-100 rounded text-red-600 group-hover:bg-red-200">
                    <LayoutDashboard className="h-3 w-3" />
                </div>
                Admin Panel
              </Link>
            </div>
          )}

          <div className="py-1">
            <Link 
              href="/orders" 
              className="group flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 hover:text-fern"
              onClick={() => setIsOpen(false)}
            >
              <ShoppingBag className="mr-3 h-4 w-4 text-gray-400 group-hover:text-fern" />
              Comenzile Mele
            </Link>
          </div>

          {user.hasStall ? (
            <div className="border-t border-gray-100 py-1">
              <Link 
                href="/seller/dashboard" 
                className="group flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 hover:text-fern"
                onClick={() => setIsOpen(false)}
              >
                <LayoutDashboard className="mr-3 h-4 w-4 text-gray-400 group-hover:text-fern" />
                Taraba Mea
              </Link>
            </div>
          ) : (
            <div className="border-t border-gray-100 py-1">
              <Link 
                href="/seller/stall/new" 
                className="group flex items-center px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 hover:text-fern"
                onClick={() => setIsOpen(false)}
              >
                <Store className="mr-3 h-4 w-4 text-gray-400 group-hover:text-fern" />
                Creaz tarab
              </Link>
            </div>
          )}

          <div className="border-t border-gray-100 py-1">
            <button 
              onClick={logout}
              className="group flex w-full items-center px-4 py-2 text-sm text-red-600 hover:bg-red-50"
            >
              <LogOut className="mr-3 h-4 w-4 text-red-400 group-hover:text-red-600" />
              Deconectare
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
