import React, { useState } from 'react';
import { Button } from './ui/button';
import { router } from '@inertiajs/react';

interface StallFormProps {
  stall: any;
  onClose: () => void;
}

const ROMANIAN_CITIES = [
  'Bucureti', 'Cluj-Napoca', 'Timioara', 'Iai', 'Constana', 
  'Craiova', 'Braov', 'Galai', 'Ploieti', 'Oradea', 'Brila',
  'Arad', 'Piteti', 'Sibiu', 'Bacu', 'Trgu Mure', 'Baia Mare',
  'Buzu', 'Botoani', 'Satu Mare'
];

export function StallForm({ stall, onClose }: StallFormProps) {
  const [values, setValues] = useState({
    name: stall.name || '',
    description: stall.description || '',
    location: stall.location || '',
  });

  const [logo, setLogo] = useState<File | null>(null);
  const [cover, setCover] = useState<File | null>(null);

  const handleChange = (e: any) => {
    setValues(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = (e: any) => {
    e.preventDefault();
    
    const formData = new FormData();
    formData.append('_method', 'PATCH');
    Object.keys(values).forEach(key => {
      formData.append(`stall[${key}]`, (values as any)[key]);
    });
    if (logo) formData.append('stall[logo]', logo);
    if (cover) formData.append('stall[cover]', cover);

    router.post('/seller/stall', formData, {
      onSuccess: () => onClose()
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label className="block text-sm font-medium text-stone-700">Nume Tarab</label>
        <input 
          type="text" 
          name="name" 
          value={values.name} 
          onChange={handleChange} 
          required 
          maxLength={50}
          className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" 
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-stone-700">Descriere</label>
        <textarea 
          name="description" 
          value={values.description} 
          onChange={handleChange} 
          required 
          rows={4}
          minLength={10}
          maxLength={1000}
          className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern" 
        />
        <p className="text-right text-[10px] text-stone-400">{values.description.length}/1000</p>
      </div>

      <div>
        <label className="block text-sm font-medium text-stone-700">Locaie</label>
        <select 
          name="location" 
          value={values.location} 
          onChange={handleChange} 
          required
          className="mt-1 block w-full rounded-md border-stone-300 shadow-sm focus:border-fern focus:ring-fern"
        >
          <option value="">Alege oraul</option>
          {ROMANIAN_CITIES.map(city => (
            <option key={city} value={city}>{city}</option>
          ))}
        </select>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block text-sm font-medium text-stone-700">Logo Tarab (Max 3MB)</label>
          <input 
            type="file" 
            accept="image/*"
            onChange={(e) => setLogo(e.target.files?.[0] || null)}
            className="mt-1 block w-full text-xs text-stone-500 file:mr-2 file:py-1 file:px-2 file:rounded-md file:border-0 file:text-xs file:font-semibold file:bg-fern/10 file:text-fern hover:file:bg-fern/20" 
          />
        </div>
        <div>
          <label className="block text-sm font-medium text-stone-700">Copert (Max 5MB)</label>
          <input 
            type="file" 
            accept="image/*"
            onChange={(e) => setCover(e.target.files?.[0] || null)}
            className="mt-1 block w-full text-xs text-stone-500 file:mr-2 file:py-1 file:px-2 file:rounded-md file:border-0 file:text-xs file:font-semibold file:bg-fern/10 file:text-fern hover:file:bg-fern/20" 
          />
        </div>
      </div>

      <div className="flex justify-end gap-3 pt-6 border-t border-stone-100 mt-4">
        <Button type="button" variant="outline" onClick={onClose}>Anuleaz</Button>
        <Button type="submit" className="bg-fern text-white">Salveaz Profilul</Button>
      </div>
    </form>
  );
}
