import { Link, router } from '@inertiajs/react';
import { AdminLayout } from '../../layouts/AdminLayout';
import { ShoppingBag, Eye, User, Store } from 'lucide-react';

interface Order {
  id: number;
  created_at: string;
  total_cents: number;
  status: string;
  buyerName: string;
  stallName: string;
  total: number;
}

interface AdminOrdersPageProps {
  orders: Order[];
}

export default function AdminOrdersPage({ orders }: AdminOrdersPageProps) {
  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-serif font-bold text-stone-800">Tranzacii</h1>
        <p className="text-stone-500">Vizualizare complet a tuturor comenzilor din platform.</p>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-stone-100 overflow-hidden">
        <table className="min-w-full divide-y divide-stone-100">
          <thead className="bg-stone-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">ID</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Dat</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Vnztor</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Cumprtor</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Total</th>
              <th className="px-6 py-3 text-left text-xs font-bold text-stone-500 uppercase">Status</th>
              <th className="px-6 py-3 text-right text-xs font-bold text-stone-500 uppercase">Aciuni</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-stone-100">
            {orders.map((order) => (
              <tr key={order.id} className="hover:bg-stone-50 transition-colors">
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-stone-900">#{order.id}</td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-stone-500">
                    {new Date(order.created_at).toLocaleDateString('ro-RO')} {new Date(order.created_at).toLocaleTimeString('ro-RO', {hour: '2-digit', minute:'2-digit'})}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-stone-900">
                    <span className="flex items-center"><Store className="h-3 w-3 mr-1 text-stone-400" /> {order.stallName}</span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-stone-900">
                    <span className="flex items-center"><User className="h-3 w-3 mr-1 text-stone-400" /> {order.buyerName}</span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-bold text-stone-900">{order.total.toFixed(2)} RON</td>
                <td className="px-6 py-4 whitespace-nowrap">
                   <span className={`text-xs px-2 py-1 rounded-full font-bold uppercase
                      ${order.status === 'completed' ? 'bg-green-100 text-green-700' :
                        order.status === 'new_order' ? 'bg-blue-100 text-blue-700' :
                        order.status === 'confirmed' ? 'bg-yellow-100 text-yellow-700' : 'bg-red-100 text-red-700'}
                   `}>
                      {order.status}
                   </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <Link href={`/admin/orders/${order.id}`} className="inline-flex items-center text-fern hover:text-fern-dark bg-green-50 px-3 py-1.5 rounded-lg border border-green-100 transition-colors">
                    <Eye className="h-4 w-4 mr-2" /> Inspecteaz
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {orders.length === 0 && <div className="p-8 text-center text-stone-500">Nu exist tranzacii.</div>}
      </div>
    </div>
  );
}

AdminOrdersPage.layout = (page: any) => <AdminLayout>{page}</AdminLayout>;
