
import { Link } from '@inertiajs/react';
import { ShoppingCart, Menu, Play } from 'lucide-react';
import { useAuth } from '../context/AuthContext';
import { useCart } from '../context/CartContext';
import { Button } from './ui/button';
import { useState } from 'react';

import NotificationBell from './NotificationBell';
import UserMenu from './UserMenu';

export function Navbar() {
  const { user, login } = useAuth();
  const { itemCount } = useCart();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  return (
    <nav className="bg-cream border-b border-stone-200 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex items-center">
            <Link href="/" className="flex-shrink-0 flex items-center select-none">
               <span className="text-2xl font-serif font-bold text-fern flex items-center">
                  <span className="mr-2"></span> Piaa Online
               </span>
            </Link>
          </div>

          <div className="hidden sm:flex sm:items-center sm:space-x-8">
             {/* Center links removed as per user request */}
             <Link href="/cum-functioneaza" className="flex items-center text-stone-600 hover:text-fern transition-colors group">
                 <div className="bg-fern/10 p-1.5 rounded-full mr-2 group-hover:bg-fern/20 transition-colors">
                    <Play className="h-4 w-4 text-fern fill-current" />
                 </div>
                 <span className="font-bold">Cum Funcioneaz</span>
              </Link>
          </div>

          <div className="hidden sm:flex sm:items-center sm:space-x-4">
             {user && <NotificationBell />}
             
             <Link href="/cart">
               <Button variant="ghost" size="icon" className="relative text-stone-600 hover:text-fern">
                 <ShoppingCart className="h-6 w-6" />
                 {itemCount > 0 && (
                   <span className="absolute -top-1 -right-1 bg-marigold text-white text-xs font-bold rounded-full h-5 w-5 flex items-center justify-center">
                     {itemCount}
                   </span>
                 )}
               </Button>
             </Link>

             {user ? (
               <UserMenu />
             ) : (
               <Button onClick={login} variant="default" className="bg-fern hover:bg-fern/90 text-white">
                  Intr n cont
               </Button>
             )}
          </div>
        </div>
      </div>
      
       {/* Mobile menu */}
      {mobileMenuOpen && (
        <div className="sm:hidden bg-cream border-t border-stone-200">
          <div className="pt-2 pb-3 space-y-1 px-4">
            <Link href="/" className="block text-stone-600 hover:text-fern py-2 text-base font-medium" onClick={() => setMobileMenuOpen(false)}>
                Acas
            </Link>
            <Link href="/cum-functioneaza" className="block text-stone-600 hover:text-fern py-2 text-base font-medium" onClick={() => setMobileMenuOpen(false)}>
                Cum Funcioneaz
            </Link>
             <div className="border-t border-stone-200 mt-2 pt-2">
                 {user ? (
                     <div className="flex items-center justify-between py-2">
                         <div className="flex items-center space-x-2">
                            <img src={user.avatar} alt={user.name} className="h-8 w-8 rounded-full" />
                            <span className="text-sm font-medium text-stone-700">{user.name}</span>
                         </div>
                         <div className="space-y-2">
                            <Link href="/orders" className="block text-sm text-stone-600" onClick={() => setMobileMenuOpen(false)}>Comenzile Mele</Link>
                             {user.hasStall && (
                                <Link href="/seller/dashboard" className="block text-sm text-stone-600" onClick={() => setMobileMenuOpen(false)}>Taraba Mea</Link>
                             )}
                         </div>
                     </div>
                 ) : (
                      <Button onClick={() => { login(); setMobileMenuOpen(false); }} className="w-full mt-2 bg-fern text-white">
                          Intr n cont
                      </Button>
                 )}
             </div>
          </div>
        </div>
      )}
    </nav>
  );
}
