import React from 'react';
import { RootLayout } from '../layouts/RootLayout';
import { Button } from '../components/ui/button';
import { Link, router } from '@inertiajs/react';
import { ClipboardList, MapPin, User, ChevronRight } from 'lucide-react';

interface SellerOrdersPageProps {
  orders: any[];
}

const statusMap: any = {
  new_order: { label: 'Comand Nou', color: 'bg-yellow-100 text-yellow-800' },
  confirmed: { label: 'Confirmat', color: 'bg-blue-100 text-blue-800' },
  completed: { label: 'Finalizat', color: 'bg-green-100 text-green-800' },
  cancelled: { label: 'Anulat', color: 'bg-red-100 text-red-800' }
};

export default function SellerOrdersPage({ orders }: SellerOrdersPageProps) {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-serif font-bold text-stone-800">Comenzi Primite</h1>
        <p className="text-stone-600">Gestioneaz comenzile primite de la clieni.</p>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-stone-100 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full text-left border-collapse">
            <thead>
              <tr className="bg-stone-50 border-b border-stone-100 text-xs uppercase font-bold text-stone-500">
                <th className="px-6 py-4">Client / Locaie</th>
                <th className="px-6 py-4">Dat</th>
                <th className="px-6 py-4 text-center">Produse</th>
                <th className="px-6 py-4">Total</th>
                <th className="px-6 py-4">Status</th>
                <th className="px-6 py-4 text-right">Aciuni</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-stone-100">
              {orders.length > 0 ? (
                orders.map((order) => (
                  <tr key={order.id} className="hover:bg-stone-50 transition-colors">
                    <td className="px-6 py-4">
                      <div className="flex flex-col">
                        <span className="font-bold text-stone-800 flex items-center">
                          <User className="h-3 w-3 mr-1" /> {order.buyerName}
                        </span>
                        <span className="text-xs text-stone-500 flex items-center">
                          <MapPin className="h-3 w-3 mr-1" /> {order.location || 'Nespecificat'}
                        </span>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-stone-600">
                      {new Date(order.createdAt).toLocaleDateString('ro-RO')}
                    </td>
                    <td className="px-6 py-4 text-center text-sm font-medium">
                      {order.itemCount}
                    </td>
                    <td className="px-6 py-4 font-bold text-stone-800">
                      {order.total} RON
                    </td>
                    <td className="px-6 py-4">
                      <span className={`px-3 py-1 rounded-full text-[10px] font-bold uppercase ${statusMap[order.status]?.color}`}>
                        {statusMap[order.status]?.label}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-right">
                       <Link href={`/seller/orders/${order.id}`}>
                         <Button variant="ghost" size="sm" className="text-fern hover:text-fern/80">
                            Detalii <ChevronRight className="h-4 w-4 ml-1" />
                         </Button>
                       </Link>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={6} className="px-6 py-12 text-center text-stone-500">
                    <div className="flex flex-col items-center justify-center">
                       <ClipboardList className="h-12 w-12 text-stone-200 mb-2" />
                       <p>Nu ai primit nicio comand nc.</p>
                       <p className="text-xs">Asigur-te c ai produse active n taraba ta.</p>
                    </div>
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

SellerOrdersPage.layout = (page: any) => <RootLayout>{page}</RootLayout>;
